// Bootstrap overrides
//
// Copy variables from `_variables.scss` to this file to override default values
// without modifying source files.
$primary: #493A96;
$secondary: #FFFFFF;
$success: #28A745;
$info: #17A2B8;
$warning: #FFC107;
$danger: #BC214C;
$light: #F8F9FA;
$dark: #343A40;
$theme-colors: ();
$theme-colors: map-merge((
        "primary": $primary,
        "secondary": $secondary,
        "success": $success,
        "info": $info,
        "warning": $warning,
        "danger": $danger,
        "light": $light,
        "dark": $dark
), $theme-colors);
$grid-breakpoints: (
        xs: 0,
        sm: 641px,
        md: 641px,
        lg: 641px,
        xl: 641px
);
$container-max-widths: (
        sm: 1170px,
        md: 1170px,
        lg: 1170px,
        xl: 1170px
);
$body-bg: #FFFFFF;
$body-color: #4A4A4A;
$link-color: #00338D;
$link-hover-color: #493A96;
$link-hover-decoration: underline;
$font-family-base: Arial, sans-serif;
$font-size-base: 1rem;
$h1-font-size: 2.25rem;
$h2-font-size: 2rem;
$h3-font-size: 1.75rem;
$h4-font-size: 1.125rem;
$h5-font-size: 0.875rem;
$h6-font-size: 0.75rem;
$line-height-base: 1.6;
$headings-font-family: Arial, sans-serif;
$headings-font-weight: 700;
$headings-line-height: 1.1;
$headings-color: #00338D;
$border-radius: 6px;
$table-cell-padding: 8px;
$table-cell-padding-sm: 8px;
$table-bg: transparent;
$table-accent-bg: #bfcce2;
$table-hover-bg: darken(#bfcce2, 5%);
$table-active-bg: transparent;
$table-border-color: transparent;
$table-head-bg: #00338d;
$table-head-color: #ffffff;
$table-striped-order: even;
$btn-font-weight: normal;
$input-bg: #FFFFFF;
$input-color: #253D86;
$input-border-color: #D9D9D9;
$input-border-radius: 6px;
$input-border-radius-lg: 6px;
$input-border-radius-sm: 6px;
$input-focus-border-color: rgba(37, 61, 134, 0.5); //#919dc2;
$btn-line-height: 1.15;
$btn-padding-y: 0.625rem;
$btn-border-radius: 6px;
$input-padding-y: 0.438rem;
$dropdown-bg: #FFFFFF;
$dropdown-border-color: transparent;
$dropdown-divider-bg: transparent;
$dropdown-link-color: #03A6FF;
$dropdown-link-hover-color: #79a3c2;
$dropdown-link-hover-bg: transparent;
$dropdown-link-active-color: #03A6FF;
$dropdown-link-active-bg: transparent;
$grid-gutter-width: 30px;
// Breadcrumbs
$breadcrumb-padding-y: 8px;
$breadcrumb-padding-x: 0px;
$breadcrumb-item-padding: 8px;
$breadcrumb-margin-bottom: 0px;
$breadcrumb-bg: transparent;
$breadcrumb-divider-color: '/';
$breadcrumb-active-color: #036AFF;
$breadcrumb-divider: "/";
$breadcrumb-border-radius: 0;
// Dropdowns
//
// Dropdown menu container and contents.
$dropdown-min-width: 0;
// Forms
$input-box-shadow: 0 0 2px 1px rgba(32, 96, 173, 0.15);
$input-focus-box-shadow: 0 0 2px 1px rgba(32, 96, 173, 0.15);
// Options
//
// Quickly modify global styling by enabling or disabling optional features.
$enable-caret: true;
$enable-rounded: true;
$enable-shadows: false;
$enable-gradients: false;
$enable-transitions: true;
$enable-hover-media-query: true;
$enable-grid-classes: true;
$enable-print-styles: true;
// Pagination
$pagination-padding-y: 2px;
$pagination-padding-x: 8px;
$pagination-padding-y-sm: 2px;
$pagination-padding-x-sm: 8px;
$pagination-padding-y-lg: 2px;
$pagination-padding-x-lg: 8px;
$pagination-line-height: 23px;
$pagination-color: #253D86;
$pagination-bg: transparent;
$pagination-border-color: transparent;
$pagination-focus-box-shadow: none;
$pagination-focus-outline: none;
$pagination-hover-color: #253D86;
$pagination-hover-bg: transparent;
$pagination-hover-border-color: transparent;
$pagination-active-color: #ffffff;
$pagination-active-bg: #253D86;
$pagination-active-border-color: #253D86;
$pagination-disabled-color: #bfcce2;
$pagination-disabled-bg: transparent;
$pagination-disabled-border-color: transparent;
