/* Portrait */
@media only screen and (min-device-width: $mac-port) and (max-device-width: $mac-land) and (orientation: portrait) {
  .public-wrapper {
    //if page height is less than 1570 than page is not scaled down properly
    min-height: 1770px !important;
  }
}
/* Landscape */
@media only screen and (min-device-width: $mac-port) and (max-device-width: $mac-land) and (orientation: landscape) {
  .public-wrapper {
    //if page height is less than 860 than page is not scaled down properly
    min-height: 960px !important;
  }
}
// Apply position relative to body for position of Cookie banner on the mobile iPhone5
@media screen and (device-aspect-ratio: 40/71) {
  body {
    position: relative;
  }
}

@media only screen and (min-device-width : 320px) and (max-device-width : 370px) and (max-device-height : 667px) {
  body {
    position: relative;
  }
}

.centered-image {
  overflow: hidden;
  img {
    position: relative;
    left: 50% !important;
    transform: translateX(-50%);
  }
}
input[type="text"]::-ms-clear {
  display: none;
}
.std-form {
  label {
    text-align: right;
    margin-bottom: 0;
    padding-top: 7px;
    font-weight: bold;
  }
  input.form-control {
    padding: 0.344rem 0.75rem;
  }
  .checkbox {
    label {
      font-weight: normal;
      cursor: pointer;
    }
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  a {
    color: inherit;
    font-size: inherit;
    line-height: inherit;
    &:hover,
    &:focus,
    &:active {
      color: inherit;
    }
  }
}
.loading-app {
  min-height: 600px;
  cursor: wait;
  position: relative;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    margin: auto;
    width: 80px;
    height: 80px;
    background: url("imgs/loading.gif") no-repeat center center;
  }
}
#cookie {
  background-color: rgba(0, 51, 141, 0.8);
  color: $white;
  @media screen and (max-width: $screen-xs) {
    //position: relative;
    padding: 15px 0 20px;
    position: absolute;
  }
  &.show {
    border-top: none;
  }
  .cookie-container {
    @media screen and (min-width: $screen-sm) {
      max-width: 940px;
    }
  }
  p {
    @include x-rem(font-size, 16px);
    line-height: 18px;
    letter-spacing: 1.23px;
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 14px);
      letter-spacing: 1.08px;
      line-height: 16px;
      margin-bottom: 15px;
      padding-top: 0;
    }
  }
  #cookieBtn {
    min-width: 232px;
    @include x-rem(font-size, 16px);
    @media screen and (max-width: $screen-xs) {
      width: 100%;
    }
  }
}

#wrapper {
  @media screen and (max-width: $screen-xs) {
    position: relative;
  }
}
main {
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    .bg-image {
      height: calc(100vh - 24px + 100px); // minus header height + cookie height
    }
    &.no-cookie {
      min-height: auto;
      .bg-image {
        height: calc(100vh - 24px); // minus header height
      }
    }
  }
}

@media only screen and (min-device-width: $mac-port) and (max-device-width: $mac-land) and (orientation: portrait) {
  .bg-image {
    min-height: 1690px;
  }
  #wrapper {
    #cookie {
      position: fixed;
    }
  }
  .cookie-modal {
    .modal-backdrop {
      background-color: transparent !important;
    }
  }
}
@media only screen and (min-device-width: $mac-port) and (max-device-width: $mac-land) and (orientation: landscape) {
  .bg-image {
    min-height: 960px;
  }
  #wrapper {
    #cookie {
      position: fixed;
    }
  }
  .cookie-modal {
    .modal-backdrop {
      background-color: transparent !important;
    }
  }
}

