a.spotlight {
  cursor: pointer;
  &:hover,
  &:focus,
  &:active {
    outline: none;
  }
}
.spotlight {
  display: block;
  margin: 0 0 25px;
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    margin: 0 0 22px;
  }
  @media screen and (max-width: $screen-xs) {
    margin: 0 0 15px;
  }
  .image-wrapper {
    display: block;
    position: relative;
  }
  img {
    display: block;
    width: 100%;
    height: auto;
    @media screen and (min-width: $above-laptop) {
      min-height: 229px;
      //max-height: 214px;//s
    }
    @media screen and (max-width: $screen-xs) {
      max-height: 194px;
    }
  }
  .spot-content {
    padding: 27px 25px 33px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      padding: 12px 20px 25px;
      min-height: 111px;
    }
    @media screen and (max-width: $screen-xs) {
      padding: 12px 15px 22px;

    }
    .spot-label {
      display: block; //inline-block
      // if label has icons, we can remove it label tag doesn't have icons
      @media screen and (min-width: $above-laptop) {
        margin-bottom: 8px;
      }
      .topic {
        padding-right: 8px;//s
        margin-right: 6px;
      }
    }
    .spot-title {
      display: block;
      margin: 16px 0;
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        margin: 8px 0;
      }
      @media screen and (max-width: $screen-xs) {
        min-height: auto;
        margin: 8px 0;
      }
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    p {
      display: block;
      margin: 10px 0 0;
      + .spot-label {
        margin-top: 10px;
      }
    }
  }
  a {
    &:hover,
    &:active {
      outline: none;
    }
  }
}
@import "skin_fixed-lines";
