.query-detail-view {
  .form-panel {
    .form-title {
      padding-left: 27px;
      &:before {
        display: none;
      }
    }
    .form-body {
      background-color: $panel-disabled-bg-color;
      > .row {
        > .col-sm {
          padding-bottom: 10px;
          @media screen and (max-width: $screen-xs) {
            padding-left: 30px;
            padding-right: 30px;
          }
          &:first-of-type {
            max-width: 27%;
            font-weight: 700;
            @media screen and (max-width: $screen-xs) {
              max-width: none;
            }
          }
          &:last-of-type {
            max-width: 70%;
            @media screen and (max-width: $screen-xs) {
              max-width: none;
            }
          }
        }
        &:last-of-type {
          > .col-sm {
            padding-bottom: 0;
          }
        }
      }
    }
  }
}