a.spotlight {
  color: $text-color-racing-green;
  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
    .spot-content {
      .spot-title {
        text-decoration: underline;
      }
    }
  }
}

.spotlight {
  //background-color: $bg-color-white;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.11);
  border-radius: 10px;
  img {
    border-radius: 10px 10px 0 0;
  }
  .spot-content {
    background-color: $bg-color-white;
    border-radius: 0 0 10px 10px;
    .spot-label {
      font-size: 0.75rem;
      line-height: 16px;
      font-weight: 700;
      > * {
        color: inherit;
      }
      .topic,
      .date {
        text-transform: uppercase;
        font-weight: normal;

      }
      .topic {
        color: $smalt;
        border-right: 1px solid;//s
      }
      /*.date {
        border-left: 1px solid transparent;//s
      }*/
    }
    .spot-title {
      font-size: 1rem;
      line-height: 22px;
      font-weight: 700;
      color: $smalt;
      display: inline-block;
      @include x-rem(font-size, 18px);
      @media screen and (max-width: $screen-xs) {
        @include x-rem(font-size, 16px);
        line-height: 20px;
      }
    }
    p {
      font-size: 0.875rem;
      line-height: 21px;
    }
  }
  a {
    color: inherit;
    &:hover,
    &:focus,
    &:active{
      color: inherit;
    }
  }
}
