.bg-jurisdiction {
  width: calc(100% + 60px);
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.07);
  padding: 130px 0 0;
  margin: -30px -30px 0;
  background: url("imgs/map.svg") no-repeat center center;
  background-size: cover;
  @media screen and (min-width: $screen-sm) {
    min-height: calc(100vh - 125px);//1220px;
  }
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    margin: -22px -22px 0;
    width: calc(100% + 44px);
    min-height: 820px;
  }
  @media screen and (max-width: $screen-xs) {
    padding: 15px 15px;
    margin: 0;
    width: 100%;
    min-height: calc(100vh - 100px);
  }
}
.jury-panel.background-panel {
  max-width: 870px;
  margin: 0 auto;
  @media screen and (min-width: $screen-sm) {
    padding: 63px 75px;
  }
  @media screen and (max-width: $screen-xs) {
    max-width: 100%;
    border-radius: 10px !important;
    padding: 20px 15px;
  }
}
.jury-panel {
  .title {
    display: block;
    @include x-rem("font-size", 33px);
    margin: 0 0 20px;
    @media screen and (max-width: $screen-xs) {
      //h4 font size
      font-size: 1.125rem;
    }
  }
  select,
  .edd-root {
    margin: 0;
  }
  p {
    line-height: 1.125;
  }
  h1 {
    padding: 7px 0 0;
    margin: 0 0 32px;
    @media screen and (max-width: $screen-xs) {
      //h4 font size
      font-size: 1.125rem;
      margin: 0 0 15px;
    }
  }
  ul,
  ol {
    margin: 0 0 30px;
    padding: 0 0 0 25px;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 15px;
    }
  }
}

.summary-panel {
  border: 1px solid $gainsboro;
  border-radius: 6px;
  background-color: $white;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.05);
  &.opinions,
  &.considerations {
    margin-bottom: 25px;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 30px;
    }
  }
  &.opinions {
    padding: 21px 12px;
    @media screen and (max-width: $screen-xs) {
      padding: 20px 13px;
    }
    .file-download-link {
      margin-bottom: 10px;
      @media screen and (max-width: $screen-xs) {
        padding-bottom: 0;
        padding-top: 0;
        margin-bottom: 9px;
      }
    }
  }
  &.considerations {
    padding: 30px 58px;
    @media screen and (max-width: $screen-xs) {
      padding: 21px 32px;
    }
    .last-update {
      display: block;
      margin: 10px 0 0 -25px;
      @media screen and (max-width: $screen-xs) {
        margin-left: -15px;
      }
      @include x-rem("font-size",12px);
      strong {
        margin-right: 3px;
      }
    }
  }
}

.summary-list {
  .standard-content {
    max-width: 100%;
  }
  .file-download-link {
    border-color: transparent;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-right: 0;
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 14px);
      line-height: 18px;
      padding: 20px 20px;
    }
    a {
      span {
        @media screen and (max-width: $screen-xs) {
          @include x-rem(font-size, 16px);
        }
      }
    }
    &:before {
      top: 12px;
      bottom: auto;
      @media screen and (max-width: $screen-xs) {
        top: -5px;
        bottom: auto;
        left: -6px;
        transform: scale(0.4);
      }
    }
  }
  h1,
  h3 {
    margin-bottom: 18px;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 10px;
    }
  }
}

.larger-select {
  select {
    padding: 21px 27px;
    height: auto;
    @media screen and (max-width: $screen-xs) {
      padding: 12px 10px;
    }
  }
  .edd-root {
    .edd-head {
      .edd-value {
        padding: 23px 0 23px 13px;
        @media screen and (max-width: $screen-xs) {
          padding: 12px 0;
        }
      }
      .edd-arrow {
        right: 16px;
        transform: scale(1.2);
        @media screen and (max-width: $screen-xs) {
          right: 0;
        }
      }
    }
  }
}

.jurisdiction-layout {
  > h1 {
    display: none !important;
  }
}

.jury-detail-wrapper {
  @media screen and (max-width: $screen-xs) {
    padding: 0 15px;
    .back-to-news-link {
      margin-left: 0;
    }
    .query-history .table-queries tbody tr td:nth-of-type(1) > span.cell-value:before {
      width: calc(100vw - 90px);
    }
  }
  .spotlights-box {
    margin-bottom: 30px;
    h2 {
      margin-top: 24px;
      margin-bottom: 12px;
      @media screen and (max-width: $screen-xs) {
        //h4 font size
        font-size: 1.125rem;
      }
    }
  }
  h1 {
    @media screen and (max-width: $screen-xs) {
      font-size: 1.125rem;
      line-height: 21px;
    }
  }
  h3 {
    @media screen and (max-width: $screen-xs) {
      //h4 font size
      font-size: 1.125rem;
    }
  }
}

.no-data {
  width: 100%;
  display: block;
  text-align: center;
  @include x-rem(font-size, 22px);
  font-weight: bold;
  color: #8c8c8c;
  padding: 54px 30px !important;
  * {
    padding: 0 !important;
    margin: 0 !important;
  }
}