.table {
  thead,
  tfoot {
    tr {
      th {
        padding: 20px 10px;
        font-weight: normal;
        text-align: center;
        vertical-align: top;
        @media screen and (min-width: $above-laptop) {
          &:first-of-type {
            padding-left: 35px;
          }
          &:last-of-type {
            padding-right: 45px;
          }
        }
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          @include x-rem(font-size, 16px);
        }
      }
    }
  }
  tbody {
    tr {
      &:hover {
        background-color: transparent;
      }
      &:nth-of-type(2n) {
        &:hover {
          background-color: #bfcce2;
        }
      }
      td {
        padding: 15px 10px;
        text-align: center;
        @media screen and (min-width: $above-laptop) {
          &:first-of-type {
            padding-left: 35px;
          }
          &:last-of-type {
            padding-right: 45px;
            .cell-value {
              text-align: right;
            }
          }
        }
        .cell-value {
          color: $charcoal;
          a {
            text-decoration: underline;
            &:hover {
              text-decoration: none;
              color: $smalt;
            }
          }
          .download-icon {
            position: relative;
            padding-left: 35px;
            @media screen and (max-width: $screen-xs) {
              padding-left: 0;
            }
            &:before {
              content: "";
              position: absolute;
              @extend .svg-pdf-file;
              left: 12px;
              top: 2px;
              @media screen and (max-width: $screen-xs) {
                display: none;
              }
            }
          }
          span {
            @media screen and (max-width: $screen-xs) {
              display: none;
            }
          }
        }
      }
    }
  }
}
.query-history {
  .query-list {
    margin: 0 15px;
    padding: 10px 15px;
    background-color: $smalt;
    color: $white;
    width: calc(100% - 30px);
  }
  .filter-row {
    display: flex;
    //width: 100%;
    background-color: $bg-color-white;
    @media screen and (max-width: $screen-xs) {
      display: block;
      position: fixed;
      top: 0;
      right: -100%;
      bottom: 0;
      left: auto;
      width: 80vw;
      height: 100vh;
      margin: auto;
      z-index: 2;
      @include mxTransition(all 0.3s ease-in-out);
    }
    + .filter-backdrop-overlay {
      display: none;
    }
    &.active {
      right: 0;
      + .filter-backdrop-overlay {
        display: block;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;
        background-color: rgba(63,63,63,0.3);
        pointer-events: none;
      }
    }
  }
  .filter-wrap {
    margin-bottom: 11px;
    @media screen and (max-width: $screen-xs) {
      flex: 0 0 100%;
    }
    &.button-wrap {
      //max-width: 136px;
      //text-align: right;
      @media screen and (max-width: $screen-xs) {
        max-width: none;
        text-align: left;
      }
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        max-width: 182px;
      }
      button {
        width: 100%;
        @media screen and (max-width: $screen-xs) {
          margin-bottom: 15px;
        }
      }
    }
  }
  .filter-options {
    @media screen and (min-width: $screen-sm) {
      position: absolute;
      top: 50px;//s
      right: 0;
      bottom: auto;
      left: 0;
      z-index: 9;
      box-shadow: 0 5px 5px 5px rgba(186, 186, 186, 0.12);
    }
    background-color: $bg-color-white;
    width: 100%;
    max-width: 100%;
    padding: 17px 20px;
    display: none;
    border: 1px solid transparent;//s
    .options-list {
      max-height: 224px;
      overflow: auto;
      &::-webkit-scrollbar {
        width: 5px;
        margin-right: 5px;
        border-right: 5px solid transparent;
      }
      /* Track */
      &::-webkit-scrollbar-track {
        //box-shadow: inset 0 0 5px grey;
        border-radius: 2px;
      }
      /* Handle */
      &::-webkit-scrollbar-thumb {
        background: $bg-color-racing-green;
        border-radius: 2px;
      }
      /* Handle on hover */
      &::-webkit-scrollbar-thumb:hover {
        background: $bg-color-racing-green;
      }
      > * {
        &.hide-me {
          display: none !important;
        }
      }
      .styled-checkbox {
        &:last-of-type {
          label {
            margin-bottom: 0;
          }
        }
      }
    }
    .styled-checkbox {
      input:focus,
      input:hover {
        + label {
          background-color: $lns-active-item-bg;
          color: $white;
        }
      }
    }
  }
  .filter-set {
    position: relative;
    .filter-title {
      border: 1px solid $gainsboro;
      border-radius: 6px;
      background-color: $bg-color-white;
      box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.05);
      font-size: 1rem;
      font-weight: 300;
      line-height: 17px;
      color: $charcoal;
      position: relative;
      @include placeHolderTextColor($charcoal);
      &:hover,
      &:focus {
        text-decoration: none;//s
        border: 1px solid rgba(37, 61, 134, 0.5);
        box-shadow: 0 0 2px 1px rgba(32, 96, 173, 0.15);
      }
      display: block;
      padding: 15px 12px 16px;
      &:hover,
      &:focus,
      &:active {
        outline: none;
        text-decoration: none;//s
      }
      &:after {
        content: "";
        @extend .svg-down-blue;
        top: 0;
        right: 14px;
        bottom: 0;
        left: auto;
        display: block;
        margin: auto;
        position: absolute;
        //height: 10px;
      }
      &.selected {
        color: $resolutionBlue;//s
      }
    }
    .clear-filter {
      position: absolute;
      top: -8px;
      right: 3px;
      bottom: 0;
      left: auto;
      z-index: 2;
      display: none;
      margin: auto;
      height: 36px;
      width: 36px;
      padding: 2px 12px;
      border-radius: 6px;
      background-color: $bg-color-white;
      &:before {
        display: block;
        position: inherit;
        top: 2px;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        content: "";
        @extend .svg-Blue_Close;
      }
      &.active {
        display: block;
      }
    }
    &.active {
      display: block;
      .filter-title {
        display: block;
      }
      .filter-options {
        display: block;
      }
      .clear-filter {
        top: 1px;
        bottom: auto;
      }
      &.multi-select {
        .filter-title:after {
          transform: rotate(180deg);//s
        }
      }
    }
    &.multi-select {
      .clear-filter {
        top: 0;
      }
      &.active {
        .clear-filter {
          top: 7px;
        }
      }
    }
  }
  .table-queries {
    @media screen and (max-width: $screen-xs) {
      display: block;
      border: none;
      margin-bottom: 22px;
      thead {
        display: none !important;
      }
      tbody {
        display: block;
        tr {
          display: flex;
          flex-wrap: wrap;
          padding: 11px 15px 9px;
          @media screen and (max-width: $screen-xs) {
            padding: 20px 15px 9px;
          }
          td {
            border: none;
            flex: 0 0 33.33333%;
            max-width: 33.33333%;
            padding: 3px 0;
            @media screen and (max-width: $screen-xs) {
              padding: 0;
            }
            &:nth-of-type(1) {
              > span.cell-value {
                position: relative;
                margin-bottom: 5px;
                padding-bottom: 5px;
              }
              > span.cell-value:before {
                content: "";
                position: absolute;
                top: auto;
                right: 0;
                bottom: -11px;
                left: 0;
                display: block;
                margin: auto;
                width: calc(100vw - 60px);
                height: 1px;
                border-bottom: 1px solid $resolutionBlue;
              }
            }
            &:nth-child(1n+4) > span.cell-label {
              margin-top: 22px;
            }
            &:nth-child(1n+4) > span.cell-value {
              margin-bottom: 10px;
            }
          }
        }
      }
    }
    tbody tr td {
      > span {
        display: block;
        font-size: inherit;
        line-height: inherit;
        @media screen and (max-width: $screen-xs) {
          @include x-rem(font-size, 12px);
          text-align: left;
          line-height: 14px;
        }
        &.cell-label {
          font-weight: bold;
          color: $resolutionBlue;
          display: none;
          @media screen and (max-width: $screen-xs) {
            display: block;
            min-height: 28px;
          }
        }
      }
    }
  }
  .mobile-filters {
    padding: 15px;
    background-color: $greyEf;
    height: 100vh;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.11);
    > div.row {
      margin-top: 30px;
    }
    .filter-back {
      display: none;
      position: relative;
      padding-left: 11px;
      &:before {
        content: "";
        @extend .svg-chevron-left;
        position: absolute;
        top: 0;
        right: auto;
        bottom: 0;
        left: 0;
        display: block;
        margin: auto;
      }
    }
    .filter-lbl {
      display: inline-block;
      color: $smalt;
      font-size: 1.125rem;
      @include x-rem(font-size, 16px);
      font-weight: bold;
      line-height: 21px;
      margin-bottom: 16px;
    }
    .filter-close {
      color: $smalt;
      font-size: 0.75rem;
      line-height: 14px;
      text-align: right;
    }
    .button-box {
      //border-top: 1px solid $gainsboro;
      text-align: center;
      padding: 15px;
      .btn {
        width: 100%;
        margin-bottom: 6px;
      }
      .group-1 {
        display: block;
        a {
          color: $smalt;
          @include x-rem(font-size, 12px);
          line-height: 14px;
        }
      }
      .group-2 {
        display: none;
      }
    }
    .filters-box {
      padding: 0;
      background-color: $bg-color-white;
      max-height: calc(100vh - 125px);//s
      max-height: calc(100vh - 180px);//s
      overflow: scroll;
    }
    .filter-box {
      border-top: 1px solid $gainsboro;
      position: relative;
      .box-title {
        display: block;
        padding: 7px 15px;
        background-color: $bg-color-white;
        span {
          display: inline-block;
          font-size: 0.75rem;
          line-height: 14px;
        }
        span.title {
          //width: calc(100% - 100px);
          color: $charcoal;
        }
        span.selection {
          //width: 94px;
          float: right;
          margin-top: 6px;
          text-align: right;
          color: #909090;
        }
        span.clear-btn {
          transform: scale(0.85);
          vertical-align: middle;
        }
        text-decoration: none;
        &:hover,
        &:focus,
        &:active {
          text-decoration: none;
        }
      }
      .box-items {
        clear: both;
        display: none;
        .custom-checkbox {
          border-top: 1px solid $gainsboro;
          position: relative;
          &:first-of-type {
            border-top: none;
          }
          label {
            margin: 0;
            padding: 13px 25px 12px 15px;
            color: $charcoal;
            font-size: 0.75rem;
            line-height: 14px;
            width: 100%;
          }
          input[type="radio"],
          input[type="checkbox"]{
            width: 0;
            height: 0;
            opacity: 0;
            position: absolute;
            &:checked {
              + label {
                color: $smalt;
                font-weight: bold;
                position: relative;
                &:after {
                  content: "";
                  @extend .svg-thin-tick;
                  position: absolute;
                  display: block;
                  top: 0;
                  right: 15px;
                  bottom: 0;
                  left: auto;
                  margin: auto;
                }
              }
              &:focus {
                & + label {
                  background-color: $bg-color-white;
                }
              }
            }
          }
        }
      }
      &.filtered {
        .box-title {
          padding: 7px 30px 7px 15px;
          /*span.title {
            width: calc(100% - 115px);
          }*/
          span.selection {
            color: $smalt;
          }
          span.clear-btn {
            position: absolute;
            right: 15px;
            top: 14px;
          }
        }
      }
    }
    &[data-type="country"],
    &[data-type="typeOfQuery"],
    &[data-type="status"],
    &[data-type="topic"],
    &[data-type="year"]{
      .filter-back {
        display: inline-block;
      }
      .button-box {
        .group-2 {
          display: block;
        }
        .group-1 {
          display: none;
        }
      }
    }
    &[data-type="country"]{
      .filter-box {
        &.country {
          .box-title {
            display: none;
          }
          .box-items {
            display: block !important;
          }
        }
        &.typeOfQuery,
        &.status,
        &.year {
          display: none;
        }
      }
    }
    &[data-type="typeOfQuery"] {
      .filter-box {
        &.typeOfQuery{
          .box-title {
            display: none;
          }
          .box-items {
            display: block !important;
          }
        }
        &.status,
        &.country ,
        &.year {
          display: none;
        }
      }
    }
    &[data-type="status"] {
      .filter-box {
        &.status{
          .box-title {
            display: none;
          }
          .box-items {
            display: block !important;
          }
        }
        &.typeOfQuery,
        &.country ,
        &.year {
          display: none;
        }
      }
    }
    &[data-type="topic"] {
      .filter-box {
        &.topic{
          .box-title {
            display: none;
          }
          .box-items {
            display: block !important;
          }
        }
        &.status,
        &.country ,
        &.year {
          display: none;
        }
      }
    }
    &[data-type="year"] {
      .filter-box {
        &.year {
          .box-title {
            display: none;
          }
          .box-items {
            display: block !important;
          }
        }
        &.topic,
        &.typeOfQuery,
        &.status,
        &.country {
          display: none;
        }
      }
    }
  }
  .filter-options{
    padding: 5px 0;
    border-radius: 4px;//s
    box-shadow: 0 1px 4px 0 rgba(0,0,0,0.22);
    .styled-checkbox {
      position: relative;
      &:hover,
      &:focus,
      &:active {
        background-color: $lns-active-item-bg;
        label {
          color: $white;
        }
      }
      label {
        margin: 0;
        padding: 6px 15px;
        color: $charcoal;
        font-size: 0.8125rem;//s
        line-height: 18px;//s
        width: 100%;
        padding-left: 20px;
      }
      input[type="checkbox"]{
        /*width: 0;
        height: 0;*/
        opacity: 0;
        position: absolute;
        &:checked {
          + label {
            color: $resolutionBlue;
            position: relative;
            &:after {
              content: "";
              @extend .svg-thin-tick;
              position: absolute;
              display: block;
              top: 0;
              left: 6px;
              bottom: 0;
              right: auto;
              margin: auto;
            }
          }
          &:focus {
            + label {
              background-color: $bg-color-white;
            }
          }
          &:hover,
          &:focus,
          &:active {
            + label {
              color: $white;
              background-color: $lns-active-item-bg;
              &:after {
                @extend .svg-thin-tick-white;
              }
            }
          }
        }
      }
    }
  }
}
body.disallow-scroll {
  overflow: hidden;
  /*height: 100vh;
  overflow-y: hidden;*/
  position: fixed;
}

body.safari-browser {
  .query-history .mobile-filters > div.row {
    margin-top: 35px;
  }
  .query-history .mobile-filters .filters-box {
    max-height: calc(100vh - 190px);
  }
}

// Users Table

.users-app-wrapper {
  .users-list {
    padding: 10px 15px;
    background-color: $smalt;
    color: $white;
  }
  .table-queries tbody tr {
    @media screen and (max-width: $screen-xs) {
      padding: 20px 6px 9px 15px;
    }
  }
  .query-history .table-queries tbody tr td > span.cell-label {
    min-height: 24px;
  }
  .query-history .table-queries tbody tr td > span.cell-value {
    min-height: 20px;
  }
  /*.query-history .table-queries tbody tr td:nth-of-type(1) > span.cell-value:before {
    bottom: -25px;
  }*/
  .query-history .table-queries tbody tr td:nth-child(1n+4) > span.cell-value {
    margin-bottom: 6px;
  }
  .table-queries tbody tr td {
    &:nth-of-type(1),
    &:nth-of-type(2) {
      @media screen and (max-width: $screen-xs) {
        flex: 0 0 32.33333%;
        max-width: 32.33333%;
        //margin-bottom: 15px;
      }
    }
    &:nth-of-type(3) {
      @media screen and (max-width: $screen-xs) {
        flex: 0 0 35.33333%;
        max-width: 35.33333%;
      }
    }
    &:last-of-type {
      span {
        a {
          @include x-rem(font-size, 16px);
          font-weight: bold;
        }
        span {
          color: $smalt;
        }
      }
      @media screen and (max-width: $screen-xs) {
        flex: 0 0 100%;
        max-width: 100%;
        .cell-label {
          display: none;
        }
        .cell-value {
          margin-top: 5px;
          a {
            @include x-rem(font-size, 14px);
            font-weight: bold;
            position: relative;
            padding-left: 20px;
            &:before {
              position: absolute;
              content: "";
              left: 0;
              right: auto;
              top: 0;
              bottom: 0;
              margin: auto;
            }
            &:first-of-type {
              float: left;
              &:before {
                // .svg-edit
                background: url("svg/sprite01.svg") 94.91525423728814% 60% no-repeat;
                width: 14px;
                height: 11px;
              }
            }
            &:last-of-type {
              float: right;
              &:before {
                // .svg-delete
                background: url("svg/sprite01.svg") 62.0253164556962% 70.17543859649123% no-repeat;
                width: 13px;
                height: 15px;
              }
            }
          }
        }
      }
    }
  }
  .table-queries tbody tr:nth-of-type(even) {
    td:last-of-type {
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 5px;
      }
    }
  }
}


.title-btn-row {
  margin-bottom: 18px;
  h2 {
    margin-top: 10px;
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 18px);
      margin-bottom: 20px;
    }
  }
  .create-user-link {
    @media screen and (max-width: $screen-xs) {
      width: 100%;
    }
  }
}

// User Dropdown

.mobile-dropdown {
  padding-left: 0;
  .dropdown-toggle {
    position: relative;
    width: 21px;
    height: 100%;
    overflow: hidden;
    display: block;
    &:after {
      position: absolute;
      @extend .svg-profile-default;
      border: none;
      margin-left: 0;
      top: 5px;
    }
  }
  .dropdown-menu {
    box-shadow: 0 1px 4px 0 rgba(0,0,0,0.22);
    a {
      color: $charcoal;
      @include x-rem(font-size, 13px);
      padding-left: 15px;
      padding-right: 15px;
    }
  }
  &.show {
    .dropdown-toggle {
      &:after {
        @extend .svg-profile-selected;
      }
    }
  }
}

// News List Styles

.news-app-wrapper {
  .query-history .filter-wrap {
    @media screen and (min-width: $screen-sm) {
      margin-bottom: 0;
      max-width: 305px;
    }
    &:last-of-type {
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 0;
      }

    }
  }
  .news-list {
    @media screen and (max-width: $screen-xs) {
      margin: 0;
    }
  }
}

// filter-app

.filter-app {
  h1 {
    border-bottom: 2px solid;
    padding-bottom: 14px;
    margin-bottom: 18px;
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
  }
  .filter-count {
    span {
      @include x-rem(font-size, 13px);
      line-height: 14px;
      margin-top: 4px;
    }
  }
  &.news-app-wrapper {
    .background-panel {
      @media screen and (min-width: $screen-sm) {
        padding-bottom: 35px;
      }
    }
  }
}

// user-content Background panel

.user-content {
  min-height: 740px;
  margin-bottom: 30px !important;
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    min-height: 466px;
  }
  @media screen and (max-width: $screen-xs) {
    min-height: 1px;
  }
  &.home-layout {
    margin-bottom: 5px !important;
  }
}

// Welcome Banner
.client-banner {
  overflow: hidden;
  background: none !important;
  .title-wrapper{
    z-index: 1;
  }
}
.welcome-banner {
  overflow: hidden;
  position: relative;
  min-height: 335px;
  border-radius: 0 0 10px 10px;
  @media (min-width: 2560px){
    min-height: 470px;
  }
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    min-height: 250px;
  }
  @media screen and (max-width: $screen-xs) {
    margin-top: 0;
    min-height: 180px;
  }
  .title-wrapper {
    z-index: 1;
    position: absolute;
    width: 91%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    display: flex;
    align-items: center;
    @media screen and (max-width: $screen-xs) {
      align-items: start;
      padding-top: 48px;
    }
    h1 {
      width: 100%;
      text-align: left;
      margin-bottom: 0;
      color: $white;
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        @include x-rem(font-size, 28px);
      }
      @media screen and (max-width: $screen-xs) {
        @include x-rem(font-size, 20px);
      }
    }
  }
  .centered-image {
    img {
      display: block;
      width: 100%;
      height: auto;
      min-height: 335px;
      max-height: 335px;
      @media (min-width: 2560px){
        min-height: 470px;
        max-height: 470px;
      }
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        min-height: 250px;
        max-height: 250px;
      }
      @media screen and (max-width: $screen-xs) {
        min-height: 180px;
        max-height: 180px;
      }
    }
  }
}

// Submit Query Row

/*.submit-query-col {
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    padding-left: 11px;
    padding-right: 11px;
    &:first-of-type {
      padding-left: 15px;
    }
    &:last-of-type {
      padding-right: 15px;
    }
  }
}*/

.submit-query-row {
  .submit-query {
    padding: 25px 20px 25px 25px;
    border-radius: 10px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      padding: 20px 22px;
    }
    &.transfer-query {
      background-color: #2F3D86;
    }
    &.lease-query {
      background-color: #482366;
    }
    &.query-history {
      background-color: #6D2977;
    }
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 15px;
      padding: 20px 15px;
    }
    .row {
      .col {
        @media screen and (max-width: 399px) {//s
          flex-basis: auto;
        }
      }
    }
    h3 {
      color: $white;
      margin-bottom: 0;
      @include x-rem(font-size, 26px);
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        @include x-rem(font-size, 18px);
      }
      @media screen and (max-width: $screen-xs) {
        @include x-rem(font-size, 16px);
        margin-bottom: 15px;
      }
    }
    .btn {
      position: relative;
      min-width: 195px;
      padding: 0.938rem 0.75rem;
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        padding: 0.438rem 0.75rem;
        min-width: 132px;
      }
      @media screen and (max-width: $screen-xs) {
        width: 100%;
        padding: 0.425rem 0.75rem;
      }
    }
  }
}

// Team Member

.team-member-col {
  /*@media (min-width: $screen-sm) and (max-width: $laptop) {
    padding-left: 1px;
  }*/
  @media screen and (max-width: $screen-xs) {
    padding-bottom: 15px;
  }
}

.carousel-inner {
  border-radius: 10px;
}

.team-member {
  padding: 30px;
  background-color: $white;
  @media screen and (min-width: $above-laptop) {
    min-height: 784px;
  }
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    padding: 22px;
    min-height: 568px;
  }
  /* ipad air 2 Portrait and Landscape */
  @media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
    min-height: 575px;
  }
  @media screen and (max-width: $screen-xs) {
    padding: 20px;
  }
  .image-wrapper {
    margin-bottom: 22px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      margin-bottom: 17px;
    }
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 15px;
    }
    img {
      width: 100%;
      display: block;
      margin: auto;
      border-radius: 5px;
      /*@media screen and (min-width: $above-laptop) {
        min-height: 392px;
        //max-height: 392px;//s
      }*/
      /*@media (min-width: $screen-sm) and (max-width: $laptop) {
        min-height: auto;
        max-height: initial;
      }
      @media screen and (max-width: $screen-xs) {
        min-height: auto;
        max-height: initial;
      }*/
    }
  }
  .team-member-details {
    margin-bottom: 55px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      margin-bottom: 35px;
    }
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 25px;
    }
    span{
      display: block;
      margin-bottom: 20px;
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        margin-bottom: 7px;
      }
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 10px;
      }
      &.name {
        color: $resolutionBlue;
        @include x-rem(font-size, 20px);
        font-weight: bold;
        margin-bottom: 12px;
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          margin-bottom: 0;
          @include x-rem(font-size, 18px);
        }
      }
      &.designation {
        margin-bottom: 27px;
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          margin-bottom: 14px;
        }
        @media screen and (max-width: $screen-xs) {
          margin-bottom: 10px;
        }
      }
      &.member-phone,
      &.mobile,
      &.email {
        padding-left: 30px;
        position: relative;
        &:before {
          position: absolute;
          content: "";
          left: 0;
          right: auto;
          top: 0;
          bottom: 0;
          margin: auto;
        }
        a {
          color: #5D5D5D;
        }
      }
      &.email {
        &:before {
          @extend .svg-email;
        }
        a {
          text-decoration: underline;
          color: $resolutionBlue;
        }
      }
      &.member-phone {
        &:before {
          @extend .svg-phone;
        }
      }
      &.mobile {
        &:before {
          @extend .svg-mobile;
        }
      }
    }
  }
}

// Team Carousel Styles

.carousel {
  position: relative;
  .carousel-indicators {
    bottom: 0;
    margin-bottom: 30px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      margin-bottom: 22px;
    }
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 22px;
    }
    li {
      width: 12px;
      height: 12px;
      background-color: #D8D8D8;
      border-radius: 50%;
      margin: 0 6px;
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        width: 9px;
        height: 9px;
      }
      &.active {
        background-color: $green-color;
      }
    }
  }
}

// Title Link Row

.read-news-title {
  margin-bottom: 15px;
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between !important;
  }
  h3 {
    margin-right: 15px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      @include x-rem(font-size, 22px);
      margin-bottom: 0;
    }
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 18px);
      float: left;
    }
  }
  > a {
    @include x-rem(font-size, 18px);
    text-decoration: underline;
    &:hover,
    &:focus,
    &:active {
      color: $resolutionBlue;
    }
    @media screen and (max-width: $screen-xs) {
      float: right;
    }
  }
}

// Team member title mobile

.team-title {
  margin-bottom: 25px;
  margin-top: 0;
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    @include x-rem(font-size, 22px);
    margin-bottom: 19px;
  }
  @media screen and (max-width: $screen-xs) {
    @include x-rem(font-size, 18px);
    margin-bottom: 10px;
  }
}