.user-account {
  background-color: $greyEf;
  @media screen and (min-width: $screen-sm) and (min-height: $innerScrollHeight) {
    height: 100vh;
    overflow: hidden;
  }
  @media only screen and (min-device-width: $mac-port) and (max-device-width: $mac-land){
    height: auto;
    overflow: auto;
  }
  .bg-jurisdiction,
  .content-wrapper,
  .left-fixed-section {
    @media screen and (min-width: $screen-sm) and (min-height: $innerScrollHeight) {
      height: 100vh;
    }
    @media only screen and (min-device-width: $mac-port) and (max-device-width: $mac-land){
      height: auto;
    }
    @media only screen and (min-device-width: $mac-port) and (max-device-width: $mac-land) and (orientation: portrait) {
      min-height: 1700px;
    }
    @media only screen and (min-device-width: $mac-port) and (max-device-width: $mac-land) and (orientation: landscape) {
      min-height: 960px;
    }
  }
  .content-wrapper {
    position: relative;
    padding-right: 0;
    padding-left: 0;
    @media screen and (max-width: $screen-xs) {
      margin-top: 50px;
    }
    header {
      padding: 34px 15px;
      width: 100%; // 75%
      background-color: $white;
      //position: fixed;
      position: relative;
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        padding: 22px 15px;
      }
      @media screen and (min-width: $screen-sm) {
        box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
        z-index: 2;
      }
      @media screen and (max-width: $screen-xs) {
        padding: 0 15px;
        position: relative;
      }
    }
    .scroll-box {
      //margin-top: 87px; // minus header height
      padding: 30px 30px 0;
      position: relative;
      @media screen and (min-width: $screen-sm) and (min-height: $innerScrollHeight) {
        max-height: calc(100vh - 124px);
        overflow-y: scroll;
      }
      @media only screen and (min-device-width: $mac-port) and (max-device-width: $mac-land){
        max-height: none;
      }
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        padding: 22px 22px 0;
      }
      @media screen and (max-width: $screen-xs) {
        margin-top: 0;
        max-height: none !important;
        overflow: auto;
        padding: 0;
      }
      .background-panel {
        @media screen and (max-width: $screen-xs) {
          border-radius: 0;
        }
      }
    }
  }
}
body.home-page {
  .user-account .content-wrapper .scroll-box {
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      padding-left: 22px;
      padding-right: 22px;
    }
  }
}
body.news-detail-page {
  .user-account .content-wrapper .scroll-box {
    padding: 30px 80px 30px 30px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      padding: 22px 80px 22px 22px;
    }
    @media screen and (max-width: $screen-xs) {
      padding: 15px 0;
    }
  }
}

body.jury-detail-page {
  .user-account .content-wrapper .scroll-box {
    padding: 30px 30px 30px 30px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      padding: 22px 22px 22px 22px;
    }
    @media screen and (max-width: $screen-xs) {
      padding: 15px 0;
    }
  }
}
