header {
  h1 {
    color: $smalt;
    //font-family: "KPMG Web", Arial, sans-serif;
    font-size: 3.75rem; //60px;
    font-weight: 300;
    line-height: 56px;
    //letter-spacing: -4px;
    font-family: KPMG-Web-Light;
    letter-spacing: 1px;
    position: relative;
    span {
      color: $curious-blue;
      @media screen and (max-width: $screen-xs) {
        color: $white;
      }
    }
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      @include x-rem(font-size, 50px);
    }
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 26px);
    }
  }
  .divider {
    display: inline-block;
    width: 1px;
    height: 53px;
    background-color: $gainsboro;
    vertical-align: top; //text-bottom
    /*position: relative;
    &:before {
      content: "";
      position: absolute;
      top: 0;
      right: auto;
      bottom: 0;
      left: 0;
      display: block;
      margin: auto;
      width: 1px;
      height: 53px;
      background-color: $gainsboro;
    }*/
  }
  &.public {
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    .logo {
      display: inline-block;
      width: 112px;
      height: auto;
      margin-right: 22px;
      vertical-align: text-bottom;
    }
    h1 {
      display: inline-block;
      margin: 0;
      vertical-align: top;
      color: $white;
      @include x-rem("font-size",40px);
      font-weight: 300;
      line-height: 1;
      font-family: KPMG-Web-Light;
      padding-left: 78px;
      position: relative;
      @media screen and (max-width: $screen-xs) {
        @include x-rem("font-size",26px);
        padding-left: 32px;
      }
      &:before {
        content: "";
        @extend .svg-kpmg-logo-blue-bg;
        position: absolute;
        top: 0;
        right: auto;
        bottom: 0;
        left: 20px;
        display: block;
        margin: auto;
        @media screen and (max-width: $screen-xs) {
          transform: scale(0.6);
          top: -6px;
          left: -6px;
        }
      }
    }
    .divider {
      @media screen and (max-width: $screen-xs) {
        display: none;
      }
    }
  }
  &.private {
    h1 {
      margin-bottom: 0;
      padding-left: 88px;
      &:before {
        display: block;
        position: absolute;
        top: 0;
        right: auto;
        bottom: 0;
        left: 0;
        margin: auto;
        content: "";
        @extend .svg-kpmg-logo-white-bg;
      }
    }
    .column-first,
    .column-last {
      vertical-align: text-bottom;
    }
    .logo {
      display: inline-block;
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        width: 140px;
        height: 29px;
      }
    }
    .divider {
      margin: 0 10px 0 23px;
    }
    .dropdown {
      vertical-align: text-bottom;
      display: inline-block;
      .dropdown-toggle {
        color: $resolutionBlue;
        font-size: 1.25rem;
        line-height: 23px;
        position: relative;
        padding-right: 24px;
        margin-top: 4px;
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          @include x-rem(font-size, 18px);
        }
        &:hover {
          text-decoration: underline;
          border-color: transparent;
          box-shadow: none;
        }
        &:focus,
        &:active {
          box-shadow: none;
          text-decoration: underline;
        }
        &:before {
          content: "";
          @extend .svg-down-blue;
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: auto;
          display: block;
          margin: auto;
        }
        &:after {
          display: none;
        }
        &[aria-expanded="true"] {
          &:before {
            transform: rotate(180deg);
          }
        }
      }
      .dropdown-menu {
        box-shadow: 0 1px 4px 0 rgba(0,0,0,0.22);
        border-radius: 4px;
        min-height: 76px;
        left: 13px !important;
        margin-top: 0;
        min-width: 231px;
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          min-width: 170px;
        }
        @media (min-width: $above-laptop){
          left: 0 !important;
        }
        a {
          font-size: 0.8125rem;
          line-height: 18px;
          color: $charcoal;
          padding: 5px 19px;
          &:hover,
          &:focus,
          &:active {
            color: $charcoal;
            text-decoration: underline;
            box-shadow: none;
          }
        }
      }
    }
    .header-title {
      position: relative;
      background-color: $smalt;
      width: calc(100% + 30px);
      margin-left: -15px;
      h1 {
        margin-bottom: 0;
        color: $white;
        @include x-rem(font-size, 26px);
        font-weight: 300;
        padding: 15px 15px 15px 49px;
        line-height: 24px;
        &:before {
          @extend .svg-kpmg-logo-blue-bg;
          transform: scale(0.6);
          left: 10px;
        }
      }
    }
  }
}

.news-items-list {
  padding-top: 30px;
  > p {
    margin-left: 15px;
    margin-right: 15px;
  }
}
.home-layout {
  margin-top: -30px;//should be equal to padding
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    margin-top: -22px;
  }
  @media screen and (max-width: $screen-xs) {
    margin: 0 0 15px;
  }
  .welcome-banner {
    margin-bottom: 30px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      margin-bottom: 22px;
    }
    @media screen and (max-width: $screen-xs) {
      margin: 0 15px 15px;
    }
  }
  .container-fluid {
    @media screen and (min-width: $screen-sm) {
      padding: 0;
    }
  }
  .submit-query-row {
    margin-bottom: 26px;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 0;
    }
  }
}
.validation-summary-errors.text-danger,
.alert-danger {
  a,
  a:hover,
  a:focus,
  a:active {
    color: inherit;
    text-decoration: underline;
  }
}

.validation-summary-errors.text-danger {
  margin-top: 5px;
  font-size: 0.875rem;
  line-height: 16px;
}

.back-to-news-link {
  position: relative;
  padding-left: 20px;
  color: $green-color;
  font-weight: bold;
  margin-bottom: 20px;
  display: inline-block;
  @include x-rem(font-size, 20px);
  margin-top: -5px;
  @media screen and (max-width: $screen-xs) {
    margin: 0 0 0 15px;
  }
  &:hover,
  &:focus,
  &:active {
    color: $green-color;
  }
  &:before {
    position: absolute;
    content: "";
    @extend .svg-green-icon;
    left: 0;
    right: auto;
    top: 0;
    bottom: 0;
    margin: auto;
  }
}

// File Download Link

.file-download-link {
  border: 1px solid $input-border-color;
  padding: 25px 25px 25px 55px;
  position: relative;
  display: block;
  border-radius: 10px;
  text-transform: uppercase;
  @include x-rem(font-size, 15px);
  margin-bottom: 20px;
  clear: both;
  max-width: 790px;
  a {
    position: relative;
    span {
      text-transform: capitalize;
      display: block;
      font-weight: bold;
      @include x-rem(font-size, 20px);
    }
  }

  &:before {
    position: absolute;
    content: "";
    left: 15px;
    right: auto;
    top: 30px;
    bottom: auto;
    margin: auto;
    @extend .svg-download;
  }
}

// Topic Deta of News Detail Page

.topic-date {
  display: block;
  text-transform: uppercase;
  padding-bottom: 15px;
  margin-bottom: 25px;
  border-bottom: 1px solid $input-border-color;
  .topic,
  .date {
    @include x-rem(font-size, 14px);
  }
  .topic {
    color: $smalt;
    margin-right: 0;
    border-right: 1px solid;
    padding-right: 4px;
  }
}// Cookie Modal Layout

#wrapper {
  .cookie-modal {
    padding-left: 0 !important;
    top: 104px;
    @media screen and (max-width: $screen-xs) {
      top: 100px;
    }
    .modal-dialog {
      min-width: 100%;
      max-width: 100%;
      margin: 0;
      .modal-content {
        border-radius: 0;
        border: none;
        padding-bottom: 70px;
        @media screen and (max-width: $screen-xs) {
          padding: 15px 0 15px;
        }
        .modal-header {
          border-bottom: none;
          @media screen and (max-width: $screen-xs) {
            padding-right: 10px;
          }
        }
        .modal-body {
          @media screen and (min-width: $screen-sm) {
            max-width: 975px;
          }
          .cookie-updated {
            @include x-rem(font-size, 15px);
            margin-bottom: 15px;
            display: inline-block;
            @media screen and (max-width: $screen-xs) {
              @include x-rem(font-size, 12px);
              margin-bottom: 8px;
            }
          }
          h1 {
            @media screen and (max-width: $screen-xs) {
              @include x-rem(font-size, 18px);
            }
          }
          h2,
          h3 {
            @media screen and (max-width: $screen-xs) {
              @include x-rem(font-size, 16px);
            }
          }
          ol,
          ul {
            padding-left: 16px;
          }
        }
        .close {
          position: relative;
          opacity: 1;
          &:before{
            position: absolute;
            content: "";
            @extend .svg-cookie-close-icon;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto;
          }
        }
      }
    }
  }
  .modal {
    height: calc(100% - 158px);
  }
}

.cookie-modal .modal-backdrop.fade.show {
  display: none !important;
}

.modal-open.cookie-modal {
  overflow: auto !important;
  header {
    z-index: 1150;
    position: fixed;
    width: 100%;
  }
  main {
    .bg-image {
      display: none;
    }
  }
  header.private + main {
    display: none;
  }
  .cookie-modal {
    padding-right: 0 !important;
  }
  #cookie {
    position: absolute;
    z-index: -2;
  }
  .modal-backdrop {
    z-index: -2;
  }
}

// Alert Dismissable

.alert-dismissible.alert-warning {
  position: relative;
  background-color: $lns-active-item-bg;
  border-color: $lns-active-item-bg;
  color: $white;
  border-radius: 10px;
  padding: 0.625rem 1.25rem;
  padding-left: 45px;
  @media screen and (max-width: $screen-xs) {
    @include x-rem(font-size, 14px);
    line-height: 16px;
    padding: 12px 52px 12px 35px;
    margin-top: 15px;//s
    margin-left: 15px;
    margin-right: 15px;
  }
  &:before {
    position: absolute;
    content: "";
    @extend .svg-tick-mark;
    left: 15px;
    right: auto;
    top: 0;
    bottom: 0;
    margin: auto;
    @media screen and (max-width: $screen-xs) {
      top: 15px;
      bottom: auto;
      left: 10px;
    }
  }
  .close {
    position: relative;
    opacity: 1;
    @media screen and (max-width: $screen-xs) {
      position: absolute;
      top: 11px;
      right: 2px;
    }
    &:before {
      position: absolute;
      content: "";
      @extend .svg-close-icon-white;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      @media screen and (max-width: $screen-xs) {
        transform: scale(0.85);
      }
    }
  }
}

