.user-account.client-strip {
  .content-wrapper {
    @media screen and (max-width: $screen-xs) {
      margin-top: 102px;
    }
    .scroll-box {
      @media screen and (min-width: $screen-sm) and (min-height: $innerScrollHeight) {
        //header height + strip lint height
        max-height: calc(100vh - 179px);
      }
      @media only screen and (min-device-width: $mac-port) and (max-device-width: $mac-land){
        max-height: none;
      }
      @media screen and (max-width: $screen-xs) {
        max-height: none !important;
      }
    }
  }
  .client-box-wrapper {
    @media screen and (max-width: $screen-xs) {
      position: fixed;
      top: 0;
      right: 0;
      left: 0;
      width: 100%;
      z-index: 3;
      padding-right: 15px;
      .dropdown.client-selector {
        position: relative !important;
      }
    }
  }
  .left-fixed-section {
    @media screen and (max-width: $screen-xs) {
      top: 50px;
    }
  }
}

.brand-wrapper {
  background-color: $smalt;
  .nomarg {
    background-color: transparent;
    padding-top: 15px;
    padding-bottom: 14px;
    @media screen and (max-width: $screen-xs) {
      padding-top: 13px;
      padding-bottom: 7px;
    }
  }
}
