.standard-content {
  @media screen and (min-width: $screen-sm) {
    max-width: 90%;
  }
  > * {
    line-height: 1.4;
  }
  p {
    margin: 0 0 10px;
  }
  /*ol,
  ul {
    padding: 0 0 10px 18px;
    margin: 0;
    li {
      padding: 0 0 5px;
      @media screen and (max-width: $screen-xs) {
        font-size: 1rem;
        line-height: 25px;
      }
    }
    &.wrapped {
      li {
        span {
          color: $text-color-racing-green;
        }
      }
    }
  }
  ul.wrapped {
    list-style-type: disc;
    li {
      color: $smalt;
      span {
        display: inline-block;
        vertical-align: top;
      }
    }
  }
*/
  ol,
  ul {
    visibility: hidden;
    padding: 0;
    margin: 0;
    display: table;
    li {
      padding: 0;
      display: table-row;
      line-height: 1.57;
      @media screen and (max-width: $screen-xs) {
        font-size: 1rem;
        line-height: 25px;
      }
    }
    &.wrapped {
      visibility: visible;
      li {
        span {
          color: $text-color-racing-green;
        }
      }
    }
  }
  ol.wrapped {
    padding: 0 0 10px 0;
    counter-reset: foo;
    li {
      counter-increment: foo;
      &:before {
        content: counter(foo) ".";
        display: table-cell;
        text-align: right;
        padding-right: 0.3em;
      }
    }
  }
  ul.wrapped {
    padding: 0 0 10px 18px;
    li {
      span {
        display: inline-block;
        position: relative;
        &:before {
          content: " ";
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background-color: $smalt;
          position: absolute;
          top: 9px;
          right: auto;
          bottom: auto;
          left: -15px;
          margin: auto;
        }
      }
    }
  }

  img {
    display: block;
    width: auto;
    height: auto;
    max-width: 100%;
    margin: 0 0 15px;
    //if image contains style attribute with float left/right
    &.left,
    &.float-left,
    &[style*="float: left;"] {
      width: auto;
      height: auto;
      float: left;
      margin: 0 25px 10px 0;
      border-radius: 8px;
      @media screen and (max-width: $screen-xs) {
        float: none;
        display: block;
        margin: 0 0 10px 0;
      }
    }

    &.right,
    &.float-right,
    &[style*="float: right;"] {
      width: auto;
      height: auto;
      float: right;
      margin: 0 0 10px 25px;
      border-radius: 8px;
      @media screen and (max-width: $screen-xs) {
        float: none;
        display: block;
        margin: 0 0 10px 0;
      }
    }
  }

  figure {
    margin: 0 0 10px;
    figcaption {
      text-align: center;
      font-style: italic;
      font-size: 0.75rem;
    }
  }

  dl {
    margin: 0 0 10px;
    display: block;
    dt,
    dd {
      display: block;
    }
    dt {
      margin: 0 0 5px;
      color: $resolutionBlue;
      @include x-rem("font-size", 20px);
      font-weight: bold;
      line-height: 23px;
      position: relative;
      @media screen and (max-width: $screen-xs) {
        @include x-rem("font-size", 16px);
        line-height: 18px;
        margin-bottom: 9px;
      }
      &:before {
        content: "";
        position: absolute;
        top: 0;
        right: auto;
        bottom: 0;
        left: -23px;
        display: block;
        margin: auto;
        @extend .svg-chevron-right;
        @media screen and (max-width: $screen-xs) {
          left: -15px;
        }
      }
    }
    dd {
      margin: 0 0 20px;
      color: $charcoal;
      @include x-rem("font-size", 16px);
      line-height: 24px;
      @media screen and (max-width: $screen-xs) {
        @include x-rem("font-size", 14px);
        line-height: 20px;
        margin: 0 0 10px;
      }
    }
  }

  meter {
    width: 100%;
    margin: 0 0 10px;
  }

  progress {
    width: 100%;
    margin: 0 0 10px;
  }

  details {
    margin: 0 0 10px;
    summary {
      font-weight: bold;
    }
    p {
      margin-left: 15px;
    }
  }

  blockquote {
    margin: 0 0 30px;
    font-size: 1.09375rem;
    &:before,
    &:after {
      font-size: 1.25rem;
      content: "";
    }
    small {
      display: block;
      line-height: 1.42857143;
      &:before {
        content: "\2014 \00A0";
      }
    }
  }

  table {
    visibility: hidden;
  }

  .table-wrapper {
    width: 100%;
    overflow-y: auto;
    padding-bottom: 6px;
    margin: 0 0 20px;
    &::-webkit-scrollbar {
      -webkit-appearance: none;
      appearance: none;
      width: 10px;
      height: 10px;
    }
    &::-webkit-scrollbar-thumb {
      border: 1px solid $border-color-silver;
      background-color: $bg-color-silver;
    }
    table {
      visibility: visible;
      width: 100%;
      margin: 0;
      @media screen and (max-width: $screen-xs) {
        width: 1000px;
        font-size: 1.0625rem;
      }
      caption {
        display: none;
      }
      thead {
        background-color: $bg-color-dim-gray;
        tr {
          th {
            color: $text-color-white;
            border-bottom: none;
            padding: 10px 20px;
          }
        }
      }
      tbody {
        tr {
          td {
            padding: 15px 20px;
            white-space: nowrap;
          }
        }
      }
    }
  }

  iframe {
    display: block;
    visibility: hidden;
  }

  .embed-responsive {
    iframe {
      visibility: visible;
    }
  }

  iframe.cke_wysiwyg_frame.cke_reset {
    //to make standard content visible in Admin WYSIWYG editor
    visibility: visible;
  }
  a {
    &.btn {
      margin: 0 10px 0 0;
      @media screen and (max-width: $screen-xs) {
        width: 100%;
        margin: 0 0 10px 0;
      }
    }
    &.btn-download {
      span,
      strong {
        display: inline-block;
        margin-right: 10px;
      }
    }
  }
  hr {
    clear: both;
    margin: 0 0 15px;
    visibility: hidden;
  }
}

.sidebar {
  background-color: $bg-color-white-smoke;
  height: 100vh;
  padding: 10px;
}

.single-news {
  .standard-content .table-wrapper {
    max-width: 768px;
    table {
      thead {
        background-color: $smalt;
        tr th {
          padding: 20px 20px;
        }
      }
      tbody {
        tr td {
          padding: 13px 20px;
        }
      }
    }
  }
}
