h3 {
  color: $resolutionBlue;
}
.btn {
  min-width: 175px;
  &.btn-secondary {
    border-color: $blueGem;
    color: $blueGem;
    font-weight: 700;
    box-shadow: 0 0 2px 1px rgba(0,0,0,0.15);
    &:hover,
    &:focus,
    &:active {
      background-color: $quartz !important;
      color: $blueGem !important;
      text-decoration: underline;
      border-color: $blueGem !important;
    }
    &[disabled="disabled"],
    &.disabled {
      color: $blueGem !important;
      background-color: $blueHaze !important;
      border: 1px solid $blueGem !important;
      opacity: 1;
      text-decoration: none;
    }
  }
  &.btn-primary {
    &:after {
      content: "";
      @extend .svg-chevron-right-white;
      display: inline-block;
      margin-left: 10px;
      @include mxTransition(0.3s);
    }
    &:hover,
    &:focus,
    &:active {
      text-decoration: underline;
    }
    &[disabled="disabled"],
    &.disabled {
      color: $blueGem;
      background-color: $blueHaze !important;
      border-color: $blueHaze;
      text-decoration: none;
      opacity: 1;
      &:after {
        display: none;
      }
    }
  }
  &.btn-large {
    min-width: 300px;
    @media (min-width: $screen-sm) and (max-width: 1755px) {
      min-width: 1px;
    }
    @media screen and (max-width: $screen-xs) {
      min-width: 1px;
    }
  }
  &.btn-clear-filter {
    min-width: 232px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      min-width: 160px;//s
      max-width: 160px;//s
    }
  }
  &.btn-tall {
    padding: 0.938rem 0.75rem;
  }
}

// Input Button
span.btn {
  &:after {
    margin-left: 5px !important;
  }
  input {
    background-color: transparent;
    border: none;
    padding: 0;
    color: $white;
    pointer-events: none;
    &:focus {
      outline: none;
    }
  }
  &:hover,
  &:focus,
  &:active {
    text-decoration: none !important;
    input {
      text-decoration: underline !important;
    }
  }
}

span.btn.focus {
  @extend span.btn:hover;
  @extend .btn-primary:hover;
}

.is-invalid.form-group {
  .form-control {
    border: 1px solid $oldRose;
    border-radius: 6px;
    background-color: $bg-color-white;
    box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.05);
    color: $oldRose;
    @include placeHolderTextColor($oldRose);
    &:focus {
      box-shadow: 0 0 0 0.2rem rgba(188, 33, 76,.25);
    }
  }
  .text-danger,
  .field-validation-error,
  .errors.help-block {
    color: $oldRose;
    font-size: 0.75rem;
    line-height: 14px;
    margin-top: 5px;
    display: inline-block;
  }

  .custom-control.custom-checkbox {
    .custom-control-label {
      color: $charcoal;
    }
  }
}
.is-valid.form-group .field-wrap {
  .form-control {
    color: $resolutionBlue;
    height: 40px;
    box-shadow: 0 0 2px 1px rgba(0,0,0,0.05);
  }
}

select.form-control {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: "";
  background: url("svg/down-blue.svg") no-repeat center right 12px;
  option {
    color: $charcoal;//s
  }
}

