  font-size: 1rem;
}*/
@mixin radioChecked{
  color: $resolutionBlue;
  border: 2px solid $resolutionBlue;
  border-radius: 6px;
  //background-color: $bg-color-white;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.05);
  background-color: $panel-disabled-checked-label;
  font-weight: bold;
  //border: 2px solid;
  &:hover {
    border: 2px solid;
  }
}
.choice-button.field-radios {
  .radio-list {
    display: flex; //display: block;
    input[type="radio"] {
      opacity: 0;
      width: 0;
      height: 0;
      &:checked {
        + label {
          @include radioChecked;
        }
        &:focus {
          & + label {
            background-color: $panel-disabled-checked-label;
          }
        }
      }
    }
    label {
      display: inline-block;
      padding: 9px 12px;
      margin: 0 20px 0 0;
      min-width: 100px;
      user-select: none;
      text-transform: capitalize;
      font-size: 0.875rem;
      font-weight: 300;
      line-height: 17px;
      text-align: center;
      color: $charcoal;
      border: 2px solid $gainsboro;
      border-radius: 6px;
      background-color: $bg-color-white;
      box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.05);
      flex: 1;
      @media screen and (max-width: $screen-xs) {
        margin: 0 10px 0 0;
        min-width: auto;
        padding: 9px 5px;
      }
      &:last-of-type {
        margin-right: 0;
      }

      &.is-checked {
        @include radioChecked;
      }
      &:hover,
      &:focus {
        color: $resolutionBlue;
        border: 2px solid $resolutionBlue;
        border-radius: 6px;
        background-color: rgba(37, 61, 134, 0.2);
        box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.05);
      }
    }
  }
}

.form-group {
  &.disabled {
    input[disabled="disabled"],
    select[disabled="disabled"],
    .is-disabled,
    .field-wrap label {
      background-color: transparent;
      &.is-checked {
        background-color: $panel-disabled-checked-label;
        font-weight: bold;
        border: 2px solid;
      }
    }
  }
}

table {
  thead {
    background-color: $smalt;
    color: $white;
    tr {
      th{
        font-size: 1.25rem;
        line-height: 23px;
      }
    }
  }
  tbody {
    tr {
      td {
        font-size: 1.0625rem;
        line-height: 29px;
      }
    }
  }
}
.pagination {
  @media screen and (min-width: $screen-sm) {
    margin-top: 30px;
    margin-bottom: -10px;
  }
  @media screen and (max-width: $screen-xs) {
    padding: 0 33px;
    float: initial !important;

  }
  .page-item {
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
    .page-link {
      border-radius: 4px;
      font-size: 1.25rem;
      //margin-right: 8px;
      margin: 0 2px;
      @media screen and (max-width: $screen-xs) {
        display: inline-block;
      }
      &.icon-left,
      &.icon-right{
        position: relative;
        height: 100%;
        &:before,
        &:after {
          position: absolute;
          content: "";
          top: 0;
          bottom: 0;
          margin: auto;
        }
      }
      &.icon-left {
        margin-right: 0;
        &:before {
          @extend .svg-chevron-right;
          transform: rotate(180deg);
          left: 0;
        }
      }
      &.icon-right {
        margin-left: 0;
        &:after {
          @extend .svg-chevron-right;
        }
      }
    }
    &:first-of-type,
    &:last-of-type,
    &.active {
      @media screen and (max-width: $screen-xs) {
        width: 33.33%;
        display: inline-block;
      }
    }
    &.active {
      @media screen and (max-width: $screen-xs) {
        text-align: center;
      }
    }
    &:last-of-type {
      @media screen and (max-width: $screen-xs) {
        text-align: right;
      }
      .page-link {
        margin-right: 0;
      }
    }
    &:nth-last-child(2) {
      .page-link {
        margin-right: 0;
      }
    }
    &.disabled {
      .icon-left {
        &:before {
          @extend .svg-chevron-right-light;
          transform: rotate(180deg);
        }
      }
      .icon-right {
        margin-left: 0;
        &:after {
          @extend .svg-chevron-right-light;
        }
      }
    }
  }
  &.news-list-pagination {
    @media screen and (min-width: $above-laptop) {
      margin: 50px 0 70px;
    }
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      margin: 40px 0;
    }
  }
}
.form-panel {
  border: 1px solid $gainsboro;
  //box-shadow: 0 5px 5px 5px rgba(186, 186, 186, 0.25);
  margin: 0 0 20px;
  border-radius: 6px;
  @media screen and (max-width: $screen-xs) {
    margin: 0 0 15px;
  }
  .form-title {
    border-radius: 6px;
    display: block;
    color: $resolutionBlue;
    font-size: 1.25rem;
    line-height: 23px;
    font-weight: normal;//s
    background-color: $bg-color-white;
    padding: 18px 40px 18px 50px;
    border-bottom: 1px solid $gainsboro;
    margin-bottom: -1px;
    position: relative;
    @media screen and (max-width: $screen-xs) {
      padding: 13px 15px;
      @include x-rem(font-size, 16px);
      line-height: 18px;//s
      font-weight: normal;//s
    }
    .form-link {
      display: none;
      font-size: 1rem;
      line-height: 18px;
      text-align: right;
      text-decoration: underline;
      float: right;
      color: $text-color-white;
      margin: 5px 0 0;
      @media screen and (max-width: $screen-xs) {
        font-size: 0.625rem;//s
        line-height: 11px;//s
        margin-top: 6px;//s
      }
      span {
        text-decoration: underline;
        position: relative;
        padding-right: 20px;
        &:after {
          width: 16px;
          height: 19px;
          @include use-glyphicon;
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: auto;
          display: block;
          margin: auto;
        }
      }
      .first {
        display: none;
        &:after {
          content: ""; //content: "\e114";
          @extend .svg-chevron-right-white;
          transform: rotate(90deg);
          -webkit-animation: rotating 2s linear infinite;
          -moz-animation: rotating 2s linear infinite;
          -ms-animation: rotating 2s linear infinite;
          -o-animation: rotating 2s linear infinite;
          animation: rotating 2s linear infinite;
        }
      }
      .last {
        display: inline-block;
        &:after {
          content: ""; //content: "\e113";
          @extend .svg-chevron-right-white;
          transform: rotate(-90deg);
        }
      }
      &.collapsed {
        border-bottom: none;
        .last {
          display: none;
        }
        .first {
          display: inline-block;
        }
      }
    }
  }
  .form-body {
    padding: 40px 35px;
    @media screen and (max-width: $screen-xs) {
      padding: 15px 0 0;
    }
    .form-group {
      margin-bottom: 20px;
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 10px;
      }
      &:last-of-type {
        margin-bottom: 0;
        @media screen and (max-width: $screen-xs) {
          margin-bottom: 0;
        }
      }
      &.field-submit {
        @media screen and (max-width: $screen-xs) {
          .field-wrap {
            flex: 0 0 100%;
            max-width: 100%;
            .btn {
              width: 100%;
              margin-bottom: 20px;
              margin-top: 10px;
            }
          }
        }
      }
    }
    &.blocked {
      background-color: $panel-disabled-bg-color;
      .form-group.field-radios .radio-list{
        position: relative;
        &:before {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          display: block;
          margin: auto;
          content: "";
          width: 100%;
          height: 100%;
          background-color: transparent;
        }
      }
    }
  }
  &.completed {
    .form-title {
      font-weight: normal;
      &:before {
        content: ""; //content: "\e013";
        @extend .svg-tick-mark;
        position: absolute;
        top: 0;
        right: auto;
        bottom: 0;
        left: 15px;
        display: block;
        margin: auto;
      }
      border-radius: 6px;
      background-color: $resolutionBlue;
      color: $text-color-white;
      @media screen and (max-width: $screen-xs) {
        padding-left: 45px;
      }
      .form-link {
        display: inline-block;
      }
    }
  }
  &.expanded {
    .form-title {
      border-radius: 6px 6px 0 0;
      font-weight: normal;//s
    }
  }
  &.testing .form-title .form-link {
    display: inline-block !important;
    color: $resolutionBlue;
    &.collapsed {
      color: $white;
    }
  }
}

// Modal Styles

.modal-dialog {
  min-width: 750px;
  max-width: 750px;
  @media screen and (max-width: $screen-xs) {
    min-width: 96%;
    max-width: 96%;
    width: 96%;
    margin: auto;
  }
  &.confirmation {
    @media screen and (min-width: $screen-sm) {
      min-width: 440px;
      max-width: 440px;
    }
  }
}
.modal-content {
  padding: 20px 40px 25px;
  box-shadow: 0 0 2px 1px rgba(0,0,0,0.05);
  @media screen and (max-width: $screen-xs) {
    padding: 2px 20px 15px;
  }
  .modal-title {
    @include x-rem(font-size, 28px);
    font-weight: bold;
    line-height: 32px;
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 18px);
    }
  }
  .modal-header,
  .modal-body,
  .modal-footer {
    padding-left: 0;
    padding-right: 0;
  }
  .modal-header {
    @media screen and (max-width: $screen-xs) {
      padding-bottom: 5px;
    }
  }
  .modal-body {
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 14px);
      padding-bottom: 5px;
      padding-top: 10px;
    }
  }
  .modal-footer {
    border-top: none;
    padding-top: 5px;
    @media screen and (max-width: $screen-xs) {
      display: flex;
      padding-bottom: 0;
    }
    .btn {
      @media screen and (max-width: $screen-xs) {
        flex: 1;
        min-width: auto;
      }
    }
    .btn-cancel-reset {
      margin-right: 16px;
    }
  }
}

// Background Panel

.background-panel {
  padding: 40px 35px;
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.11);
  background-color: $white;
  @media screen and (max-width: $screen-xs) {
    padding: 15px;
    margin-top: 15px;
  }
}

// Left Fixed section

.left-fixed-section {
  position: relative;
  background: url("imgs/Navigation_Desktop.jpg") no-repeat;
  background-position: center center;
  background-color: $lns-bg;
  width: 100%;
  padding: 35px 0 0;
  color: $white;
  @media screen and (min-width: $above-laptop) {
    min-height: 100vh;
    display: -webkit-box !important;
    display: -ms-flexbox !important;
    display: flex !important;
    -ms-flex-preferred-size: auto;
    flex-basis: auto;
    flex-grow: 0;
    flex-direction: column;
  }
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    //min-height: 768px;
    //max-height: 768px;
    //background: url("imgs/Navigation_Laptop.jpg") no-repeat;
    min-height: 820px;
    background: none;
    background-color: $lns-bg;
    &:before,
    &:after {
      content: "";
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
    }
    &:before {
      background: url("imgs/side_nav_background.png") no-repeat;
      z-index: 1;
    }
    &:after {
      //background-color: rgba(0, 16, 149, 0.9);
      background-color: $lns-bg;
      opacity: 0.8;
      z-index: 2;
    }
    > * {
      z-index: 3;
      position: relative;
    }
  }
  @media screen and (max-width: $screen-xs) {
    padding: 15px 0 0;
    background: none;
    background-color: $white;
    min-height: auto;
    position: fixed;
    z-index: 2;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.1);
    top: 0;
  }
  .logo-row {
    margin: 0;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 10px;
    }
  }
  .collapsing {
    display: none;
  }
  button {
    height: 25px;
    width: 25px;
    margin: 0;
    padding: 0;
    position: relative;
    @media screen and (max-width: $screen-xs) {
      height: 20px;
      width: 20px;
    }
    &:before {
      position: absolute;
      content: "";
      @extend .svg-menu-icon;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
    }
  }
  &.nav-open {
    background-color: $lns-bg;
    min-height: 125vh;
    @media screen and (max-width: $screen-xs) {
      background: url("imgs/Naivgation_Mobile.jpg") no-repeat center center;
      background-size: cover;
      background-color: $lns-bg;
      position: relative;
    }
    .navbar-collapse {
      min-height: 357px !important;
    }
    .collapsing {
      position: initial;
      display: block;
    }
    button {
      &:before {
        @extend .svg-close-icon;
      }
    }
    .main-logo,
    .client-logo,
    .mobile-dropdown {
      display: none !important;
    }
  }
  .main-logo {
    margin-bottom: 35px;
    padding-left: 50px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      margin-bottom: 30px;
      padding-left: 22px;
    }
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 0;
      padding: 0;
    }
    a {
      @extend .svg-kpmg-white;
      display: block;
      @media (min-width: $screen-sm) and (max-width: $laptop) {
        // .svg-kpmg-white-laptop
        background: url("svg/sprite01.svg") 100% 0 no-repeat;
        width: 100px;
        height: 40px;
      }
      @media screen and (max-width: $screen-xs) {
        // .svg-kpmg-mobile
        background: url("svg/sprite01.svg") 0 73.61963190184049% no-repeat;
        width: 56px;
        height: 23px;
      }
    }
  }

  // Navigation

  ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
    li {
      a {
        color: $white;
        display: block;
      }
    }
    &.left-nav {
      li {
        margin-bottom: 6px;
        padding-left: 50px;
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          padding-left: 22px;
          margin-bottom: 2px;
        }
        @media screen and (max-width: $screen-xs) {
          padding-left: 35px;
          margin-bottom: 10px;
        }
        a {
          @include x-rem(font-size, 24px);
          line-height: 28px;
          padding: 15px 0;
          @media (min-width: $screen-sm) and (max-width: $laptop) {
            padding: 15px 0;
            @include x-rem(font-size, 18px);
          }
          @media screen and (max-width: $screen-xs) {
            @include x-rem(font-size, 18px);
            line-height: 21px;
          }
          &:hover,
          &:focus,
          &:active {
            outline: none;
            text-decoration: underline;
          }
        }
        &.active {
          box-shadow: 0 0 4px 0 rgba(0,0,0,0.15);
          background-color: $lns-active-item-bg;
          a {
            font-weight: bold;
            padding: 15px 0;
          }
        }
      }
    }
    &.left-nav-bottom {
      padding-bottom: 1px;
      li {
        margin-bottom: 10px;
        a {
          @include x-rem(font-size, 18px);
          line-height: 21px;
          @media (min-width: $screen-sm) and (max-width: $laptop) {
            @include x-rem(font-size, 16px);
          }
          @media screen and (max-width: $screen-xs) {
            @include x-rem(font-size, 14px);
            line-height: 16px;
          }
          &:hover,
          &:focus,
          &:active {
            outline: none;
            text-decoration: underline;
          }
        }
        &.active {
          a {
            text-decoration: underline;
            font-weight: bold;
          }
        }
      }
    }
  }
}

