// Log Layout Header

.nomarg {
  padding: 15px 2.3% 10px;
  background-color: #ffffff;
  min-height: 100%;
  //border-bottom: solid 1px #95989a;
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    padding: 18px 2.5%;
  }
  @media screen and (max-width: $screen-xs) {
    padding: 15px 4% 8px;
  }
}

.title-with-bg {
  background-color: $smalt;
  color: $white;
  span {
    padding: 13px 0;
    display: block;
    font-family: KPMG-Web-Light;
    @include x-rem(font-size, 26px);
    font-weight: 300;
    letter-spacing: 1px;
    line-height: 24px;
  }
}

// Print Button

.btn-print {
  width: 61px;
  height: 60px;
  background-color: $resolutionBlue;
  border-radius: 10px 0 0 10px;
  display: inline-block;
  @include mxTransition(0.1s);
  &:hover {
    width: 64px;
    background-color: $lns-active-item-bg;
  }
  @media screen and (max-width: $screen-xs) {
    display: none;
  }
  .btn-print-wrapper {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%;
    &:before {
      position: absolute;
      content: "";
      @extend .svg-print;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
    }
  }
}

.user-content {
  > h1,
  h1 {
    @media screen and (max-width: $screen-xs) {
      font-size:  1.125rem;//s
      line-height: 21px;//s
    }
  }
  > h2,
  h2,
  h3,
  h3{
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 16px);
      line-height: 16px;
    }
  }
}
.navbar-collapse {
  @media (min-width: $screen-sm) and (max-width: $laptop){
    height: calc(100% - 70px);
  }
}

textarea.form-control {
  height: auto !important;
}

.custom-control.custom-checkbox {
  padding-left: 0;
  .custom-control-input {
    &:checked {
      ~ label{
        &:before {
          background-color: $white;
        }
        &:after {
          left: 2px;
          top: 3px;
          width: 11px;
          height: 9px;
          background: url("svg/checked-tick.svg") no-repeat !important;

        }
      }
    }
    &:active,
    &:focus {
      ~ label a {
        text-decoration: none;
      }
      ~ label:before {
        background-color: $white;
        box-shadow: 0 0 2px 1px rgba(32, 96, 173, 0.15);
        border-color: rgba(37, 61, 134, 0.5);
      }
    }
  }
  label {
    padding-left: 21px;
    &:before {
      width: 15px;
      height: 15px;
      border: 1px solid $gainsboro;
      border-radius: 3px;
      background-color: $white;
      box-shadow: 0 0 2px 1px rgba(0,0,0,0.05);
      top: 0;
      left: 0;
    }
  }
}
.terms-group {
  background-color: transparent;
  .custom-checkbox {
    min-height: 14px;
  }
  label {
    color: $charcoal;
    font-size: 0.75rem;
    line-height: 14px;
    a {
      color: $blueGem;
      text-decoration: underline;
      &:hover,
      &:focus {
        text-decoration: none;
      }
    }
  }
}

.client-logo {
  img {
    display: block;
    width: auto;
    height: auto;
    max-width: 60px;
    //max-height: 12px;
  }
}

.logo-client {
  display: block;
  width: auto;
  height: auto;
  max-width: 167px;
  //max-height: 34px;
}

.carousel-team {
  .carousel-inner {
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.11);
  }
}

.panel-bg-grey {
  position: relative;
  padding: 14px 15px 14px 0;
  min-height: 50px;
  background-color: $greyEf;
  @media screen and (max-width: $screen-xs) {
    padding: 12px 0;
  }
}

.dropdown.client-selector {
  display: inline-block;
  @media screen and (max-width: $screen-xs) {
    position: inherit;
  }
  .label-text,
  .dropdown-toggle{
    display: inline-block;
    vertical-align: middle;
    color: $resolutionBlue;
    font-size: 1.25rem;
    line-height: 23px;
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      @include x-rem(font-size, 18px);
    }
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 14px);
    }
  }
  .dropdown-toggle {
    min-width: 1px;
    font-weight: bold;
    position: relative;
    padding: 0 24px 0 0;
    &:hover {
      text-decoration: underline;
      border-color: transparent;
      box-shadow: none;
    }
    &:focus,
    &:active {
      box-shadow: none;
      text-decoration: underline;
    }
    &:before {
      content: "";
      @extend .svg-down-blue;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: auto;
      display: block;
      margin: auto;
    }
    &:after {
      display: none;
    }
    &[aria-expanded="true"] {
      &:before {
        transform: rotate(180deg);
      }
    }
  }
  .dropdown-menu {
    box-shadow: 0 1px 4px 0 rgba(0,0,0,0.22);
    border-radius: 4px;
    min-width: 234px;
    //min-height: 250px;
    @media screen and (max-width: $screen-xs) {
      margin: 0 !important;
      min-width: 100vw;
    }
    &.show {
      top: 31px;
      right: 0;
      left: auto;
      @media screen and (max-width: $screen-xs) {
        top: 38px;
        right: -15px;
      }
    }
    a {
      font-size: 0.8125rem;
      line-height: 18px;
      color: $charcoal;
      padding: 5px 19px;
      &:hover,
      &:focus,
      &:active {
        color: $charcoal;
        text-decoration: underline;
        box-shadow: none;
      }
    }
  }
}