//your plugins go here
$column-one: 45.5%;
$column-two: 33.7%;
$column-three: 20.08%;
@mixin leftGap($width) {
  @if $width == $column-one {
    margin-left: calc(45.5% + 15px);
  }
}
.vue-form-app {
  margin-bottom: 60px;
  .vue-form-generator {
    @media screen and (min-width: $fullHd) {
      max-width: 85.589%; //s
    }
    @media (min-width: $screen-sm) and (max-width: $laptop) {
      max-width: none; //s
    }
    .form-group {
      display: flex;
      flex-wrap: wrap;
      label,
      .field-wrap,
      .hint {
        display: inline-block;
        padding-left: 15px;
        padding-right: 15px;
      }
      > label {
        flex: 0 0 $column-one; //s
        max-width: $column-one; //s
        @include x-rem(font-size, 15px);
        .tool-tip {
          display: none;
          @media screen and (max-width: $screen-xs) {
            display: inline-block;
            position: relative;
            span.new {
              display: none;
              position: absolute;
              background-color: $white;
              padding: 20px;
              top: -35px;
              left: 45px;
              border-radius: 5px;
              margin: auto;
              width: 230px; //calc(100% - 40px);
              color: $charcoal;
              box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.1);
              z-index: 1;
              &:before {
                position: absolute;
                content: "";
                left: -11px;
                top: 20px;
                // .svg-tooltip-arrow
                background: url("svg/sprite01.svg") 98.31932773109244% 24.539877300613497% no-repeat;
                width: 12px;
                height: 23px;
              }
            }
            .icon {
              display: block;
              width: 34px;
              height: 34px;
              position: absolute;
              top: 0;
              right: auto;
              bottom: 0;
              left: 0;
              margin: auto;
              padding: 2px 7px;
              &:before {
                content: "";
                display: inline-block;
                // .svg-info
                background: url("svg/sprite01.svg") 27.94759825327511% 86.66666666666667% no-repeat;
                width: 21px;
                height: 21px;
              }
              &:focus {
                + span.new {
                  display: block;
                }
              }
            }
            &.active {
              span.new {
                display: block;
              }
            }
          }
        }
        @media screen and (max-width: $screen-xs) {
          flex: 0 0 100%;
          max-width: 100%;
          @include x-rem(font-size, 14px);
        }
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          flex: 0 0 40%; //s
          max-width: 40%; //s
        }
        > span.help {
          display: inline-block;
          float: left;
          font-style: italic;
          text-transform: capitalize;
          .icon {
            display: none !important;
          }
          .helpText:after {
            content: "-";
            display: inline-block;
            padding: 0 5px;
          }
        }
      }
      .field-wrap {
        flex: 0 0 $column-two; //s
        max-width: $column-two; //s
        margin: 0;
        @media screen and (max-width: $screen-xs) {
          flex: 0 0 100%;
          max-width: 100%;
        }
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          flex: 0 0 50%; //s
          max-width: 50%; //s
        }
      }
      .errors.help-block {
        max-width: 32.5%;
        width: 32.5%;
        margin-left: 45.5%;
        padding: 0 15px;
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          max-width: 48%;
          width: 48%;
          margin-left: 40%;
        }
        @media screen and (max-width: $screen-xs) {
          max-width: 100%;
          width: 100%;
          margin-left: 0;
        }
      }
      .hint {
        flex: 0 0 $column-three;//s
        max-width: $column-three;//s
        color: transparent;
        @media screen and (max-width: $screen-xs) {
          flex: 0 0 100%;
          max-width: 100%;
          display: none;
        }
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          flex: 0 0 10%; //s
          max-width: 10%; //s
        }
      }
      .buttons {
        button {
          margin: 0 10px 0 0;
        }
      }
      span.hint-text {
        display: block;
        background-color: $white;
        padding: 19px 23px;
        margin: 15px 0 0;
        border-radius: 5px;
        color: $charcoal;
        box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.1);
        z-index: 1;
        @include x-rem(font-size, 14px);
        line-height: 18px;
      }
    }
    .buttons-row {
      .field-wrap {
        text-align: right;
        margin-left: $column-one;
        @media screen and (max-width: $screen-xs) {
          margin-left: 0;
        }
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          margin-left: 40%;
        }
      }
    }
    .panel-buttons.field-checkbox {
      position: relative;
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 22px;
      }
      label {
        position: absolute;
        z-index: -1;
      }
      .left-gap {
        flex: 0 0 $column-one; //s
        max-width: $column-one; //s
        @media screen and (max-width: $screen-xs) {
          flex: 0 0 100%;
          max-width: 100%;
          @include x-rem(font-size, 14px);
        }
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          flex: 0 0 40%; //s
          max-width: 40%; //s
        }
      }
      .field-wrap {
        position: relative;
        input[type="checkbox"]{
          visibility: hidden;
          position: absolute;
          z-index: -1;
        }
        .btn {
          width: 100%;
          &.btn-edit {
            display: none;
          }
        }
      }
    }
    .blocked {
      .panel-buttons.field-checkbox {
        .field-wrap {
          .btn {
            &.btn-continue {
              display: none;
            }
            &.btn-edit {
              display: block;
            }
          }
        }
      }
    }
    .group-buttons {
      display: flex;
      .btn {
        flex: 1;
        margin-right: 20px;
        &:last-child{
          margin-right: 0;
        }
        @media screen and (max-width: $screen-xs) {
          min-width: 1px;
        }
      }
    }
  }
  .hint {
    position: relative;
    span.icon {
      display: block;
      width: 21px;
      height: 21px;
      position: absolute;
      top: 9px; //s
      right: auto;
      bottom: auto;
      left: 0;
      margin: auto;
      &:before {
        content: "";
        @extend .svg-info;
        display: inline-block;
      }
      &:after {
        @extend .svg-tooltip-arrow;
        @media (min-width: $screen-sm) and (max-width: $laptop) {
          display: none;
          content: "";
          position: absolute;
          margin: auto;
          top: auto;
          right: 0;
          bottom: -22px;
          left: 0;
          transform: rotate(90deg);
          z-index: 9;
        }
      }
      &:hover,
      &:focus {
        outline: none;
        &:after {
          @media (min-width: $screen-sm) and (max-width: $laptop){
            display: block;
          }
        }
        + span.new {
          display: block;
        }
      }
    }
    span.new {
      display: none;
      position: absolute;
      background-color: $white;
      padding: 20px;
      top: -10px;
      left: 45px;
      border-radius: 5px;
      margin: auto;
      width: 320px; //calc(100% - 40px);
      color: $charcoal;
      box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.1);
      z-index: 1;
      @include x-rem(font-size, 14px);
      &:before {
        position: absolute;
        content: "";
        left: -11px;
        top: 20px;
        @extend .svg-tooltip-arrow;
      }
      @media (min-width: $screen-sm) and (max-width: $laptop){
        top: 45px;
        right: 0;
        left: auto;
        width: 440px;
        &:before {
          opacity: 0;
          left: auto;
          top: -17px;
          right: 20%;//70px;
          bottom: auto;
          transform: rotate(90deg);
        }
      }
    }
  }
  /*.divider-group.form-group {
    //position: relative;
    //padding-top: 50px;
    &:before {
      content: "";
      width: calc(100% - 30px);
      height: 1px;
      position: absolute;
      top: 0;
      right: 0;
      bottom: auto;
      left: 0;
      display: block;
      margin: auto;
      background-color: $bg-color-racing-green;
    }
  }*/
  .form-panel {
    .form-body {
      fieldset legend {
        display: none !important;
      }
    }
  }
  select.form-control {
    padding-right: 30px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    &::-ms-expand {
      display: none;
    }
  }
}
// Individual Tooltip Styles
.hint {
  flex: 0 0 $column-three;//s
  max-width: $column-three;//s
  @media screen and (max-width: $screen-xs) {
    flex: 0 0 100%;
    max-width: 100%;
    //display: none;
  }
}
.form-tooltip {
  position: relative;
  height: 32px;
  @media screen and (max-width: $screen-xs) {
    display: none;
  }
  span.icon {
    display: block;
    width: 34px;
    height: 34px;
    position: absolute;
    top: 0;
    right: auto;
    bottom: 0;
    left: 0;
    margin: auto;
    padding: 7px;
    &:before {
      content: "";
      @extend .svg-info;
      display: inline-block;
    }
    &:hover,
    &:focus {
      + span.new {
        display: block;
      }
    }
  }
  span.new {
    display: none;
    position: absolute;
    background-color: $white;
    padding: 20px;
    top: -15px;
    left: 45px;
    border-radius: 5px;
    margin: auto;
    width: 329px; //calc(100% - 40px);
    color: $charcoal;
    box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.1);
    z-index: 1;
    @include x-rem(font-size, 14px);
    &:before {
      position: absolute;
      content: "";
      left: -11px;
      top: 20px;
      @extend .svg-tooltip-arrow;
    }
  }
}
// Form Fields text color
.form-control:focus {
  color: #253D86;
}
.is-invalid {
  label {
    color: $oldRose;
  }
}
// Login, Forget and Reset blocks
.login-forget-reset-block {
  border-radius: 6px;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.05);
  background-color: rgba(255, 255, 255, 0.85);
  border: 1px solid $input-border-color;
  padding: 45px 50px;
  width: 443px;
  margin: auto;
  @media screen and (max-width: $screen-xs) {
    width: 92%;
    padding: 35px 13px 15px;
  }
  > h1 {
    text-align: center;
    margin-bottom: 22px;
    margin-top: 6px;
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 18px);
      margin-bottom: 18px;
      margin-top: 0;
    }
  }
  .form-group {
    margin-bottom: 20px;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 5px;
    }
    label {
      margin-bottom: 3px;
      @media screen and (max-width: $screen-xs) {
        @include x-rem(font-size, 14px);
        margin-bottom: 0;
      }
    }
    &.terms-group {
      margin-top: -7px;
      margin-bottom: 15px;
      @media screen and (max-width: $screen-xs) {
        margin-top: 12px;
        margin-bottom: -11px;
      }
    }
  }
  input {
    padding: 0.438rem 0.75rem;
    @media screen and (max-width: $screen-xs) {
      padding: 0.282rem 0.75rem;
    }
  }
  input.form-control,
  input[type="text"],
  input[type="email"],
  input[type="password"] {
    height: 40px;
  }
  .btn {
    width: 100%;
    margin-bottom: 15px;
    @media screen and (max-width: $screen-xs) {
      margin-top: 20px;
      margin-bottom: 12px;
    }
  }
  .cta-link {
    display: table;
    margin: auto;
    color: $resolutionBlue;
    @media screen and (max-width: $screen-xs) {
      @include x-rem(font-size, 14px);
    }
  }
}
.bg-image {
  background: url("imgs/Log-In-Image_Desktop.jpg") no-repeat top center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: calc(100vh - 160px);
  position: relative;
  @media screen and (min-width: $screen-sm) {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  @media screen and (max-width: $screen-xs) {
    //height: calc(100vh - 170px); //calc(100vh - 100px)
    height: calc(100vh - 99px);
    background: url("imgs/Log-In-Image_Mobile.jpg") no-repeat top center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    margin-top: 0;
    //height: 400px;
  }
  @media only screen and (min-device-width : 320px) and (max-device-width : 370px) and (max-device-height : 667px) {
    //min-height: 100vh;
    min-height: 575px;
  }
  @media screen and (device-aspect-ratio: 40/71) {
    //min-height: 100vh;
    min-height: 575px;
  }
  /* ----------- iPhone 6, 6S, 7 and 8 ----------- */
  @media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2) {
    height: calc(100vh - 50px);
  }
  .login-forget-reset-block {
    /*bottom: 50% !important;
    transform: translateY(50%);*/
    /*position: absolute;
    left: 0;
    right: 0;
    top: 180px;
    margin: auto;*/
    @media screen and (max-width: $screen-xs) {
      position: absolute;
      left: 0;
      right: 0;
      margin: auto;
      top: 40px;
    }
    &.reset-password {
      > h1 {
        white-space: nowrap;//s
      }
    }
  }
}
.no-cookie {
  .bg-image {
    @media screen and (max-width: $screen-xs) {
      height: calc(100vh - 99px);
    }
  }
  main {
    min-height: auto;
  }
}
// Create User Panel
.user-form {
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.05);
  width: 82%;
  max-width: 82%;
  @media (min-width: $screen-sm) and (max-width: $laptop) {
    width: 100%;
    max-width: 100%;
  }
  @media screen and (max-width: $screen-xs) {
    width: 100%;
    max-width: 100%;
  }
  .form-title {
    font-weight: bold;
  }
  .form-body {
    padding-bottom: 20px;
    input.form-control,
    input[type="text"],
    input[type="email"],
    input[type="password"] {
      height: 40px;
    }
    .validation-summary-errors {
      margin-bottom: 38px;
      @media screen and (max-width: $screen-xs) {
        margin-left: 15px;
        margin-right: 15px;
        margin-bottom: 13px;
      }
    }
  }
  .form-body .form-group {
    @media screen and (min-width: $screen-sm) {
      label {
        .form-tooltip {
          display: none;
        }
      }
    }
    @media screen and (max-width: $screen-xs) {
      margin: 0;
      label {
        .form-tooltip {
          display: inline-block;
          span.icon {
            top: 18px;
            &:focus,
            &:active {
              + span.new {
                display: block;
                width: 190px;
                top: -8px;
              }
            }
          }
        }
      }
    }
    .editing-form-label-cell {
      label {
        @media screen and (min-width: $screen-sm) {
          padding-left: 40px;
        }
      }
    }
  }
  .form-body {
    .btn {
      width: 100%;
      @media screen and (max-width: $screen-xs) {
        margin-top: 20px;
      }
    }
  }
}
// Verify Login error styles
.validation-summary-errors {
  ul {
    padding: 0;
    li {
      list-style: none;
      @include x-rem(font-size, 14px);
      line-height: 16px;
    }
  }
}

.alert-dismissible + .user-content > p {
  @media screen and (max-width: $screen-xs) {
    @include x-rem(font-size, 14px);
  }
}