.spotlight.fixed-lines {
  .spot-content {
    .spot-label{
      @include fixLines(2, 16px, 0, 16px);//s
    }
    .spot-title {
      @include fixLines(2, 22px, 0, 22px);
      @media screen and (max-width: $screen-xs) {
        @include fixLines(2, 20px, 2, 20px);
      }
    }
    p {
      @include fixLines(4, 21px);
    }
  }
}
